.counter{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 5%;
}
.counts h1{
    margin: 100px;
    font-size: 2.5rem;
}
.buttonsi{
    display: flex;
    gap: 50px;
}
.buttonsi button{
    background-color: black;
    color: white;
    font-size: 3rem;
    cursor: pointer;
    padding: 10px 50px;
}